.bridge-game {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    font-family: 'Prompt', sans-serif;
    background: linear-gradient(135deg, #f5f7fa, #e4efe9);
    color: #212529;
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.game-header {
    display: flex;
    flex-direction: column;
    margin-bottom: 25px;
    background: linear-gradient(135deg, #0d3b66, #1a237e);
    border-radius: 15px;
    padding: 25px;
    color: white;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.game-header::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 100 100"><path d="M10,30 L20,60 L30,50 L40,80 L50,30 L60,70 L70,40 L80,60 L90,50" fill="none" stroke="rgba(255,255,255,0.1)" stroke-width="2"/></svg>');
    background-size: 150px;
    opacity: 0.5;
}

.game-header h1 {
    font-size: 2.5rem;
    margin: 0;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
    font-weight: 800;
    color: #ffffff;
    text-align: center;
    position: relative;
    display: inline-block;
}

.game-header h1::after {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 100%;
    height: 3px;
    background: linear-gradient(90deg, transparent, #ff9800, transparent);
}

.level-info {
    margin-top: 15px;
    padding: 15px;
    background: rgba(0, 0, 0, 0.2);
    border-radius: 12px;
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    animation: fadeIn 0.5s ease-in-out;
    color: white;
}

@keyframes fadeIn {
    from { opacity: 0; transform: translateY(-10px); }
    to { opacity: 1; transform: translateY(0); }
}

.level-info h2 {
    margin: 0 0 10px 0;
    font-size: 1.8rem;
    font-weight: 700;
    color: #ffeb3b;
    text-shadow: 0 2px 3px rgba(0, 0, 0, 0.5);
    letter-spacing: 0.5px;
}

.level-description {
    font-size: 1.1rem;
    line-height: 1.5;
    margin: 0;
    color: #ffffff;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
    font-weight: 500;
    background: rgba(0, 0, 0, 0.1);
    padding: 10px 15px;
    border-radius: 8px;
}

.game-container {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
}

.game-stats {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    background: white;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.game-stats > div {
    background: #f8f9fa;
    padding: 12px 15px;
    border-radius: 10px;
    font-weight: 600;
    flex: 1;
    min-width: 150px;
    text-align: center;
    border: 1px solid #e9ecef;
    transition: all 0.3s ease;
    color: #343a40;
}

.game-stats > div:hover {
    transform: translateY(-3px);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
    background: linear-gradient(135deg, #f8f9fa, #e9ecef);
}

.game-stats span {
    display: block;
    font-size: 1.5rem;
    font-weight: 800;
    margin-top: 5px;
    background: linear-gradient(135deg, #3a0ca3, #4361ee);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: none;
}

.environment-info {
    background: white;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    position: relative;
    overflow: hidden;
}

.environment-info::before {
    content: '🌍';
    position: absolute;
    right: 20px;
    top: 20px;
    font-size: 2rem;
    opacity: 0.2;
}

.environment-info h3 {
    margin: 0 0 15px 0;
    font-size: 1.5rem;
    color: #212529;
    position: relative;
    padding-bottom: 10px;
    font-weight: 700;
    letter-spacing: 0.5px;
}

.environment-info h3::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 50px;
    height: 3px;
    background: linear-gradient(90deg, #4a00e0, #8e2de2);
    border-radius: 3px;
}

.environment-info p {
    margin: 10px 0;
    font-size: 1rem;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.environment-info p span {
    background: #f1f3f5;
    padding: 5px 10px;
    border-radius: 20px;
    display: inline-flex;
    align-items: center;
    gap: 5px;
    font-size: 0.9rem;
    transition: all 0.3s ease;
    color: #343a40;
    font-weight: 600;
    border: 1px solid #dee2e6;
}

.environment-info p span:hover {
    background: #e9ecef;
    transform: translateY(-2px);
}

.special-conditions {
    background: #fff8e1 !important;
    color: #ff6f00 !important;
    padding: 10px 15px !important;
    border-left: 4px solid #ff9800 !important;
    border-radius: 5px !important;
    margin-top: 15px;
    font-weight: 600 !important;
    display: block !important;
    letter-spacing: 0.3px;
}

.materials-panel {
    background: white;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.materials-panel h3 {
    margin: 0 0 15px 0;
    font-size: 1.5rem;
    color: #212529;
    position: relative;
    padding-bottom: 10px;
    font-weight: 700;
    letter-spacing: 0.5px;
}

.materials-panel h3::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 50px;
    height: 3px;
    background: linear-gradient(90deg, #4a00e0, #8e2de2);
    border-radius: 3px;
}

.materials-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 15px;
}

.material-item {
    background: #f8f9fa;
    border-radius: 12px;
    padding: 15px;
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
    border: 1px solid #e9ecef;
}

.material-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    border-color: #dee2e6;
    background: linear-gradient(135deg, #f8f9fa, #e9ecef);
}

.material-info {
    display: block;
    margin-bottom: 10px;
    font-weight: 600;
    color: #212529;
    letter-spacing: 0.3px;
}

.material-description {
    font-size: 0.85rem;
    color: #495057;
    margin: 8px 0;
    line-height: 1.5;
}

.material-item button {
    background: linear-gradient(135deg, #4a00e0, #8e2de2);
    color: white;
    border: none;
    padding: 8px 15px;
    border-radius: 20px;
    cursor: pointer;
    font-weight: 500;
    transition: all 0.3s ease;
    margin-top: 5px;
    display: inline-flex;
    align-items: center;
    gap: 5px;
}

.material-item button::before {
    content: '➕';
    font-size: 0.9rem;
}

.material-item button:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 5px 10px rgba(74, 0, 224, 0.3);
}

.material-item button:disabled {
    background: #ced4da;
    cursor: not-allowed;
}

.bridge-construction {
    background: white;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    position: relative;
}

.bridge-construction h3 {
    margin: 0 0 15px 0;
    font-size: 1.5rem;
    color: #212529;
    position: relative;
    padding-bottom: 10px;
    font-weight: 700;
    letter-spacing: 0.5px;
}

.bridge-construction h3::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 50px;
    height: 3px;
    background: linear-gradient(90deg, #4a00e0, #8e2de2);
    border-radius: 3px;
}

.bridge-preview {
    min-height: 150px;
    background: linear-gradient(180deg, #e3f2fd, #bbdefb);
    border-radius: 10px;
    padding: 20px;
    margin: 15px 0;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
}

.bridge-preview::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 30px;
    background: linear-gradient(180deg, rgba(25, 118, 210, 0.2), rgba(25, 118, 210, 0.6));
    border-radius: 0 0 10px 10px;
    animation: waveAnimation 3s infinite linear;
}

@keyframes waveAnimation {
    0% { transform: translateX(-50px); }
    100% { transform: translateX(50px); }
}

.bridge-part {
    background: white;
    border-radius: 8px;
    padding: 10px 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
}

.bridge-part:hover {
    transform: scale(1.05);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
}

.bridge-part span {
    font-weight: 600;
    color: #212529;
}

.bridge-part button {
    background: #f8d7da;
    color: #721c24;
    border: none;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 0.8rem;
    transition: all 0.3s ease;
}

.bridge-part button::before {
    content: '✕';
}

.bridge-part button:hover:not(:disabled) {
    background: #e35d6a;
    color: white;
    transform: rotate(90deg);
}

.bridge-part button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.empty-bridge {
    color: #6c757d;
    font-style: italic;
    text-align: center;
    width: 100%;
}

.bridge-strength {
    text-align: center;
    font-size: 1.2rem;
    font-weight: 700;
    color: #3a0ca3;
    margin-top: 15px;
    padding: 10px;
    background: #e6f4ff;
    border-radius: 10px;
    border: 1px solid #b6e0fe;
    letter-spacing: 0.5px;
}

.test-results {
    background: white;
    border-radius: 15px;
    padding: 25px;
    margin: 20px 0;
    text-align: center;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    position: relative;
    overflow: hidden;
    animation: slideIn 0.5s ease-out;
}

@keyframes slideIn {
    from { opacity: 0; transform: translateY(30px); }
    to { opacity: 1; transform: translateY(0); }
}

.test-results::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
}

.test-results.success::before {
    background: linear-gradient(90deg, #66bb6a, #43a047);
}

.test-results.failure::before {
    background: linear-gradient(90deg, #ef5350, #e53935);
}

.test-results h3 {
    margin-top: 10px;
    font-size: 1.8rem;
    font-weight: 800;
    letter-spacing: 0.5px;
}

.test-results.success h3 {
    color: #2e7d32;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}

.test-results.failure h3 {
    color: #c62828;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}

.test-results p {
    margin: 5px 0;
    font-size: 1.1rem;
    color: #212529;
    font-weight: 500;
}

.success-buttons {
    margin-top: 20px;
}

.next-level {
    background: linear-gradient(135deg, #66bb6a, #43a047);
    color: white;
    border: none;
    padding: 12px 25px;
    border-radius: 25px;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 10px rgba(67, 160, 71, 0.3);
    display: inline-flex;
    align-items: center;
    gap: 8px;
    letter-spacing: 0.5px;
}

.next-level::after {
    content: '👉';
}

.next-level:hover {
    transform: translateY(-3px);
    box-shadow: 0 7px 15px rgba(67, 160, 71, 0.4);
}

.game-completed {
    text-align: center;
    padding: 25px;
    animation: confettiBackground 10s infinite;
}

@keyframes confettiBackground {
    0%, 100% { background-color: rgba(255, 193, 7, 0.1); }
    25% { background-color: rgba(76, 175, 80, 0.1); }
    50% { background-color: rgba(33, 150, 243, 0.1); }
    75% { background-color: rgba(156, 39, 176, 0.1); }
}

.game-completed h3 {
    color: #3a0ca3;
    font-size: 2rem;
    margin-bottom: 20px;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
    font-weight: 800;
    letter-spacing: 0.5px;
}

.game-completed p {
    font-size: 1.3rem;
    margin-bottom: 25px;
    color: #212529;
    font-weight: 600;
}

.game-over-buttons {
    display: flex;
    justify-content: center;
    gap: 15px;
}

.game-over-buttons button {
    background: linear-gradient(135deg, #4a00e0, #8e2de2);
    color: white;
    border: none;
    padding: 12px 25px;
    border-radius: 25px;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 10px rgba(142, 45, 226, 0.3);
    letter-spacing: 0.5px;
}

.game-over-buttons button:first-child {
    background: linear-gradient(135deg, #ff9800, #f57c00);
    box-shadow: 0 4px 10px rgba(245, 124, 0, 0.3);
}

.game-over-buttons button:hover {
    transform: translateY(-3px);
    box-shadow: 0 7px 15px rgba(0, 0, 0, 0.2);
}

.control-panel {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 15px;
    margin-top: 20px;
}

.control-button {
    padding: 15px;
    border: none;
    border-radius: 12px;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    letter-spacing: 0.5px;
}

.simulate {
    background: linear-gradient(135deg, #66bb6a, #43a047);
    color: white;
}

.simulate::before {
    content: '🔍';
}

.reset {
    background: linear-gradient(135deg, #ff9800, #f57c00);
    color: white;
}

.reset::before {
    content: '🔄';
}

.home {
    background: linear-gradient(135deg, #2196f3, #1976d2);
    color: white;
}

.home::before {
    content: '🏠';
}

.control-button:hover:not(:disabled) {
    transform: translateY(-5px);
    box-shadow: 0 7px 15px rgba(0, 0, 0, 0.15);
}

.control-button:disabled {
    background: #ced4da;
    color: #6c757d;
    cursor: not-allowed;
    transform: none;
    box-shadow: none;
}

@media (max-width: 768px) {
    .bridge-game {
        padding: 15px;
        font-size: 14px;
    }

    .game-header {
        padding: 20px;
    }
    
    .game-header h1 {
        font-size: 1.8rem;
        letter-spacing: 0.5px;
    }

    .level-info h2 {
        font-size: 1.4rem;
    }

    .level-description {
        font-size: 1rem;
    }

    .materials-list {
        grid-template-columns: 1fr;
    }

    .game-stats {
        flex-direction: column;
        gap: 10px;
    }

    .game-stats > div {
        min-width: auto;
    }

    .test-results h3 {
        font-size: 1.5rem;
    }

    .next-level, .game-over-buttons button {
        padding: 10px 20px;
        font-size: 1rem;
    }

    .environment-info h3,
    .materials-panel h3,
    .bridge-construction h3 {
        font-size: 1.3rem;
    }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
    .bridge-game {
        background: linear-gradient(135deg, #1a1a2e, #16213e);
        color: #e6e6e6;
    }
    
    .game-stats > div, .material-item, .bridge-construction, .test-results,
    .materials-panel, .environment-info {
        background: #0f3460;
        border-color: #1a1a2e;
        color: #e6e6e6;
    }
    
    .game-stats span {
        background: linear-gradient(135deg, #9c27b0, #bb86fc);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    
    .environment-info h3, .materials-panel h3, .bridge-construction h3 {
        color: #e9ecef;
    }
    
    .bridge-preview {
        background: linear-gradient(180deg, #16213e, #0f3460);
    }
    
    .bridge-part {
        background: #1a1a2e;
        color: #e6e6e6;
    }
    
    .bridge-strength {
        background: #1a1a2e;
        color: #bb86fc;
    }
    
    .control-button:disabled {
        background: #2c2c44;
        color: #919191;
    }
    
    .special-conditions {
        background: #3e2723 !important;
        color: #ffcc80 !important;
        border-left-color: #ff9800 !important;
    }
    
    .material-info {
        color: #e9ecef;
    }
    
    .material-description {
        color: #cbd5e0;
    }
    
    .bridge-part span {
        color: #e9ecef;
    }
    
    .test-results h3 {
        text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    }
    
    .test-results p {
        color: #e9ecef;
    }
    
    .game-completed h3 {
        color: #bb86fc;
    }
    
    .game-completed p {
        color: #e9ecef;
    }
}